 .InlineImageNode__contentEditable {
    min-height: 20px;
    border: 0px;
    resize: none;
    cursor: text;
    caret-color: rgb(5, 5, 5);
    display: block;
    position: relative;
    tab-size: 1;
    outline: 0px;
    padding: 10px;
    user-select: text;
    font-size: 14px;
    line-height: 1.4em;
    width: calc(100% - 20px);
    white-space: pre-wrap;
    word-break: break-word;
  }
  .ContentEditable__root[contenteditable="false"] .InlineImageNode__placeholder {
    display: none;
  }
  .InlineImageNode__placeholder {
    font-size: 12px;
    color: #888;
    overflow: hidden;
    position: absolute;
    text-overflow: ellipsis;
    bottom: 10px;
    left: 10px;
    user-select: none;
    white-space: nowrap;
    display: inline-block;
    pointer-events: none;
    margin-left: auto;
    margin-right: auto;
    width: calc(100% - 20px);
    text-align: center;
  }
  
  .image-control-wrapper--resizing {
    touch-action: none;
  }